.wp-block-zolo-google-map {
    --zolo-map-height: clamp(18.75rem, 50vw, 43.75rem);
    --zolo-marker-info-margin: 10px 0 0;
    --zolo-gm-style-padding: 15px;
    .zolo-gmap-wrapper {
        height: var(--zolo-map-height);
        overflow: hidden;
        .gm-style-iw.gm-style-iw-c {
            max-width: 300px !important;
            line-height: 1.4;
            padding: var(--zolo-gm-style-padding);
            top: -35px;
        }

        .gm-style-iw-tc {
            top: -35px;
        }

        button.gm-ui-hover-effect {
            top: 0 !important;
            right: 0 !important;
        }

        .zolo-gmap-marker-info {
            margin: var(--zolo-marker-info-margin);
        }
    }
}
